.forum-label {

  $block-name:                &; // #{$block-name}__element {}

  padding: 4px 8px;
  border-radius: 4px;
  font-size: $font-size--small;
  line-height: 1.3334;
  color: $white;
  background: $color-warning-light;

  svg {
    display: inline-block;
    vertical-align: middle;
    fill: $white;
  }

  &__icon {
    margin-right: 4px;
  }

}
